.sidebar {
  width: 200px; /* Adjust width */
  height: 100vh;
  background-color: #2c3e50;
  color: white;
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: auto;
  padding: 20px;
}

.sidebar-title {
  font-size: 20px;
  font-weight: bold;
  text-align: center;
  margin-bottom: 20px;
}

.sidebar-menu {
  list-style: none;
  padding: 0;
}

.sidebar-menu li {
  margin: 15px 0;
}

.sidebar-link {
  display: flex;
  align-items: center;
  text-decoration: none;
  color: white;
  font-size: 16px;
  padding: 10px;
  border-radius: 5px;
  transition: background 0.3s ease-in-out;
}

.sidebar-link:hover,
.sidebar-link.active {
  background: #1abc9c;
}

.icon {
  margin-right: 10px;
}
