/* üåü Global Dashboard Styling */
body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  transition: background-color 0.3s ease, color 0.3s ease;
}

/* üåû Light Theme */
[data-theme="light"] {
  background-color: #f4f4f9;
  color: #333;
}

/* üåô Dark Theme */
[data-theme="dark"] {
  background-color: #000000; /* üî• Dark mode background is now BLACK */
  color: white;
}

/* üîπ Dark Mode - Apply Theme to Components */
[data-theme="dark"] .settings-container,
[data-theme="dark"] .card,
[data-theme="dark"] .filters-container select,
[data-theme="dark"] .filters-container input {
  background-color: #1a1a1a;
  color: white;
  border: 1px solid #444;
}

[data-theme="dark"] .sidebar {
  background-color: #111;
}

[data-theme="dark"] .sidebar-menu li a {
  color: white;
}

[data-theme="dark"] .sidebar-menu li a:hover {
  background: rgba(255, 255, 255, 0.2);
}

/* ‚öôÔ∏è Settings Page Styling */
.settings-container {
  max-width: 300px;
  margin: 20px auto;
  padding: 20px;
  background: white;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
}

.settings-container h2 {
  margin-bottom: 4px;
}

.settings-container select {
  padding: 8px;
  font-size: 16px;
  margin-top: 4px;
}

/* üè† Main Dashboard Container */
.dashboard-container {
  max-width: 1200px;
  margin: auto;
  padding: 20px;
}

/* üî∑ Dashboard Title */
.dashboard-title {
  text-align: center;
  font-size: 28px;
  font-weight: bold;
  color: #1976d2;
  margin-bottom: 20px;
}

/* üéõÔ∏è Filters Section */
.filters-container {
  display: flex;
  gap: 10px;
  justify-content: center;
  flex-wrap: wrap;
  margin-bottom: 20px;
}

.filters-container select,
.filters-container input {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 14px;
  background-color: #fff;
  cursor: pointer;
}

/* üìä Metrics Grid */
.metrics-grid {
  display: flex;
  gap: 20px;
  justify-content: center;
  margin-bottom: 20px;
  flex-wrap: wrap;
}

/* üìà Charts & Insights Grid */
.charts-grid,
.insights-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
  margin-top: 20px;
  padding: 10px;
}

/* üè∑Ô∏è Dashboard Cards */
.card {
  background: white;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  text-align: center;
  transition: transform 0.2s ease-in-out;
}

.card:hover {
  transform: translateY(-5px);
}

/* üé® Custom Colors for Cards */
.card:nth-child(1) { background: #d1ecf1; } /* Light Blue */
.card:nth-child(2) { background: #d4edda; } /* Light Green */
.card:nth-child(3) { background: #f8d7da; } /* Light Red */

/* ‚ö° Sidebar Styling */
.sidebar {
  width: 200px;
  height: 100vh;
  background-color: #2c3e50;
  color: white;
  position: fixed;
  left: 0;
  top: 0;
  display: flex;
  flex-direction: column;
  padding-top: 20px;
}

.sidebar-title {
  text-align: center;
  font-size: 22px;
  font-weight: bold;
}

.sidebar-menu {
  list-style: none;
  padding: 0;
}

.sidebar-menu li {
  padding: 12px 20px;
}

.sidebar-menu li a {
  text-decoration: none;
  color: white;
  display: flex;
  align-items: center;
}

.sidebar-menu li a:hover {
  background: rgba(255, 255, 255, 0.2);
  border-radius: 5px;
}

/* üèóÔ∏è Sidebar Active State */
.sidebar-menu li a.active {
  background: #1976d2;
  border-radius: 5px;
}

/* ‚ú® Fix Sidebar Width Issue */
.content {
  margin-left: 230px; /* Adjusted to match sidebar width */
  padding: 20px;
  flex-grow: 1;
}

/* ‚ú® Make Everything Responsive */
@media (max-width: 1024px) {
  .charts-grid,
  .insights-grid {
    grid-template-columns: 1fr;
  }

  .filters-container {
    flex-direction: column;
    align-items: center;
  }

  .sidebar {
    width: 200px;
  }

  .content {
    margin-left: 200px;
  }
}
